	module.exports = {"data":{"markdownRemark":{"html":"<p>整理堆积如山的文件，又看到H君夏天写来的信，觉得很有灵气：</p>\n<p>\"只能很落寞地，一个人站在南方一个小城的一个小小的窗前，日复一日地，看着大风把雨帘吹得四处飘散，看着那些豆大的雨点吧嗒吧嗒地落在花坛松散的泥土里和那些想飞的花儿的娇嫩的翅膀上。\"\"日子悠然地而往复地向后逝去，往前延伸。每天做些内省冥想的功课，以整理繁复的记忆，回味缤纷的感觉。也有时，放弃了思维的缰绳，舒展了身心，沉醉在渺渺的梵乐声中，任由空谷来音引领着漫入无忧无虑、无烦无恼、无知无觉的无我境界。\"</p>\n<p>\"想着昔日的同窗，这会儿个个都在为生存奔波，只有我还静享着天赐的安宁，悠然地坐在竹板凳上，纳凉，饮茶，读圣贤书，做DAYDREAM，的确也是福份了。更尤感这些宝贵的光阴应当好好珍惜。\"</p>\n<p>人知道自足，有时是最大的幸福源泉，内心的平静，胜过一切满足。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0171.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0171.md","frontmatter":{"title":"10月29日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0171.md absPath of file >>> MarkdownRemark"}}

